}
@font-face {
  font-family: "Poppins";
  src: url("assets/fonts/Poppins-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
}
html {
  background-color: white;
  scroll-behavior: smooth;
  height: 100%;
}

body {
  font-family: "Jost", sans-serif;
  font-style: normal;
}

svg path, svg rect, svg circle {
  transition: all 0.3s ease-in-out;
}

.container {
  padding: 0 40px;
  width: 100%;
  max-width: 1920px;
  margin-inline: auto;
}
@media (max-width: 767px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  height: 62px;
  padding: 0 30px;
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
  cursor: pointer;
  border: 1px solid transparent;
  background-color: red;
  color: #fff;
  font-weight: 600;
  font-size: 24px;
}
@media (min-width: 768px) {
  .btn:hover {
    background-color: gray;
  }
}
@media (max-width: 1600px) {
  .btn {
    height: 44px;
    padding: 0 24px;
    font-size: 16px;
  }
}
@media (max-width: 767px) {
  .btn {
    height: 37px;
    padding: 0 20px;
    font-size: 14px;
  }
}